import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './css/Header.css';

function Header(props){
  const [click, setClick] = useState(false);
  const texts = ["デザイン事務所 ピクス" ,"わたしたちについて", "作品一覧", "お問い合わせ・ご依頼", "これまでのお仕事"];
  const closeMenu = () => {
    setClick(false);
  }

  useEffect(() => {
    document.getElementById('current').addEventListener('click',closeMenu);
    closeMenu();
    return(() => {
      document.getElementById('current').removeEventListener('click',closeMenu);
    })
  },[props.pageNumber])

  const links = ["/", "/Aboutus", "/Library", "/Contact", "/Works"];
  const linkTexts = ["Top", "Aboutus", "Library", "Contact", "Works"];
  

  const linkItems = links.map((item,index) => 
    <>
      <Link to={links[index]} className={props.pageNumber === index ? `links active page${props.pageNumber}` : `links page${props.pageNumber}`} id={props.pageNumber === index ? "current" : ""} onClick={props.setPageNumberindex}>
        {linkTexts[index]}
      </Link>
    </>
  )

  return(
    <>
      <div className={`header page${props.pageNumber}`}>
        <Link className="header-link" to="/" onClick={props.setPageNumber0}>
          <img src={`${process.env.PUBLIC_URL}/img/icon-img.png`} alt="" />
        </Link>
        <h1>{texts[props.pageNumber]}</h1>
      </div>
      <div className="menu-icon">
        <i className={click ? 'fas fa-times' : 'fas fa-bars'} onClick={() => setClick(!click)} />
      </div>
      <div className={click ? 'linkButton active' : 'linkButton'}>
        {linkItems}
      </div>
    </>
  )
}

export default Header;